const Icon = ({ name, ...props }) => {
  const ChosenIcon = icons[name]

  if (!ChosenIcon) {
    throw new Error(`Cannot find icon '${name}'`)
  }

  return (
    <svg className="Icon" {...props} viewBox={ChosenIcon.viewBox}>
      {ChosenIcon.path}
    </svg>
  )
}

Icon.propTypes = {
  name: t.string,
  size: t.number,
  style: t.object,
}

export default Icon

